  computed: {
    getSliderBlue_1 () {
      return `background-image: -webkit-gradient(linear, 0% 100%, 100% 100%, color-stop(${this.blue_1}, rgb(0, 159, 232)), color-stop(${this.blue_1}, #a6e0ff))`
    },
    getSliderBlue_2 () {
      return `background-image: -webkit-gradient(linear, 0% 100%, 100% 100%, color-stop(${this.blue_2}, rgb(0, 159, 232)), color-stop(${this.blue_2}, #a6e0ff))`
    },
    getSliderBlue_3 () {
      return `background-image: -webkit-gradient(linear, 0% 100%, 100% 100%, color-stop(${this.blue_3}, rgb(0, 159, 232)), color-stop(${this.blue_3}, #a6e0ff))`
    }
  },
  mounted () {
    this.step = 0
    this.tabItem = 1
    this.nextImage()
    this.fetchTab(this.tabItem)
    this.setTimer()
  },
  methods: {
    setTimer () {
      this.startCounter = setInterval(() => {
        if (this.step === this.phoneImage[this.tabItem - 1].step.length) {
          return this.fetchTab(this.tabItem)
        }
        this.nextImage()
      }, 3500)
    },
    nextImage () {
      this.isPhoneShow = false
      if (this.step === this.phoneImage[this.tabItem - 1].step.length) return
      this.step++
      this.content = this.phoneImage[this.tabItem - 1].step[this.step - 1].content
      this.thisImage = this.phoneImage[this.tabItem - 1].step[this.step - 1].img
      if (this.step === this.phoneImage[this.tabItem - 1].step.length || this.step === 1) {
        this.bgLine_4 += 0.12
        this.bgLine_5 += 0.13
        this.bgLine_6 += 0.08
        this.bgLine_9 += 0.15
      } else {
        this.bgLine_4 += 0.23
        this.bgLine_5 += 0.25
        this.bgLine_6 += 0.16
        this.bgLine_9 += 0.3
      }

      if (this.step > 6) {
        if (this.step === this.phoneImage[this.tabItem - 1].step.length || this.step === 1) {
          this.bgLine_10 += 0.13
        } else {
          this.bgLine_10 += 0.24
        }
      }
      setInterval(() => {
        this.isPhoneShow = true
      }, 3500)
    },
    preImage () {
      this.isPhoneShow = false
      if (this.step === 1) return
      this.step--
      this.content = this.phoneImage[this.tabItem - 1].step[this.step - 1].content
      this.thisImage = this.phoneImage[this.tabItem - 1].step[this.step - 1].img
      setInterval(() => {
        this.isPhoneShow = true
      }, 3500)
    },
    fetchTab (num) {
      this.isPhoneShow = false
      this.tabItem = num
      this.step = 1
      this.bgLine_4 = 0.13
      this.bgLine_5 = 0.13
      this.bgLine_6 = 0.1
      this.bgLine_9 = 0.1
      this.bgLine_10 = 0.1
      this.stepNumber = this.phoneImage[this.tabItem - 1].step.length
      this.content = this.phoneImage[this.tabItem - 1].step[this.step - 1].content
      this.thisImage = this.phoneImage[this.tabItem - 1].step[this.step - 1].img
      setInterval(() => {
        this.isPhoneShow = true
      }, 3500)
    },
    goStep (e) {
      this.step = e.target.innerText.split('')[1]
      this.content = this.phoneImage[this.tabItem - 1].step[this.step - 1].content
      this.thisImage = this.phoneImage[this.tabItem - 1].step[this.step - 1].img
    },
    toggleMenu () {
      this.isOpen = !this.isOpen
    },
    openMbMenu () {
      this.isMbOpen = !this.isMbOpen
    },
    getActiveNumber (val) {
      if (this.step >= val) {
        return 'active_number'
      }
      return 'normal_number'
    },
    goTop () {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    },
    changeSlider (val) {
      this.total = 0
      this.cost = 0
      this.profit = 0
      if (val === 1) {
        this.sliderValue_1 = this.value_1
        this.blue_1 = Number(Number(this.sliderValue_1) / 20000)
      } else if (val === 2) {
        this.sliderValue_2 = this.value_2
        this.blue_2 = Number((Number(this.sliderValue_2) - 5) / 7)
      } else if (val === 3) {
        this.sliderValue_3 = this.value_3
        this.blue_3 = Number(Number(this.sliderValue_3) / 40)
      }
      const month = this.sliderValue_3 * 12
      const rate = this.sliderValue_2 / 100
      for (let i = 1; i <= month; i++) {
        const each = this.sliderValue_1 * Math.pow((1 + rate / 12), i)
        this.total += each
      }
      const t = Math.round(this.total)
      this.total = this.toCurrency(t)
      const n = this.sliderValue_1 * this.sliderValue_3 * 12
      this.cost = this.toCurrency(n)
      this.profit = this.toCurrency(t - n)
    },
    inputSlider (val) {
      this.total = 0
      this.cost = 0
      this.profit = 0
      if (val === 1) {
        this.value_1 = this.sliderValue_1
        this.blue_1 = Number(Number(this.sliderValue_1) / 20000)
      } else if (val === 2) {
        this.value_2 = this.sliderValue_2
        this.blue_2 = Number((Number(this.sliderValue_2) - 5) / 7)
      } else if (val === 3) {
        this.value_3 = this.sliderValue_3
        this.blue_3 = Number(Number(this.sliderValue_3) / 40)
      }
      const month = this.sliderValue_3 * 12
      const rate = this.sliderValue_2 / 100
      for (let i = 1; i <= month; i++) {
        const each = this.sliderValue_1 * Math.pow((1 + rate / 12), i)
        this.total += each
      }
      const t = Math.round(this.total)
      this.total = this.toCurrency(t)
      const n = this.sliderValue_1 * this.sliderValue_3 * 12
      this.cost = this.toCurrency(n)
      this.profit = this.toCurrency(t - n)
    },
    toCurrency (num) {
      var parts = num.toString().split('.')
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      return parts.join('.')
    },
    newPage (val) {
      if (val === 'fans') {
        window.open('https://www.facebook.com/yuantabank/')
      } else if (val === 'line') {
        window.open('https://line.me/R/ti/p/%40mwr3999i')
      } else if (val === 'ig') {
        window.open('https://www.instagram.com/yuantabank/')
      } else if (val === 'yt') {
        window.open('https://www.youtube.com/channel/UCs72_Ba4-zxCCaeF7zsoAoA')
      } else if (val === 'tg') {
        window.open('https://t.me/yuantabanktw')
      }
    },
    scrollTo (refName) {
      const element = this.$refs[refName]
      let top = ''
      if (refName === 'step_6') {
        top = 8500
        this.isOpen = false
        this.isMbOpen = false
      } else {
        top = element.offsetTop
        this.isMbOpen = false
      }
      document.documentElement.scrollTo({
        top: top,
        left: 0,
        behavior: 'smooth'
      })
    },
    scrollRight () {
      var container = document.getElementById('container')
      this.sideScroll(container, 'right', 25, 100, 10)
    },
    scrollLeft () {
      var container = document.getElementById('container')
      this.sideScroll(container, 'left', 25, 100, 10)
    },
    sideScroll (element, direction, speed, distance, step) {
      let scrollAmount = 0
      var slideTimer = setInterval(function () {
        if (direction === 'left') {
          element.scrollLeft -= step
        } else {
          element.scrollLeft += step
        }
        scrollAmount += step
        if (scrollAmount >= distance) {
          window.clearInterval(slideTimer)
        }
      }, speed)
    },
    openPage () {
      window.open('https://onelink.to/z3ucjp')
    }
  }
